$htmlSize: 14;

@function toRem($value) {
	$remValue: calc($value / $htmlSize); 
	@return $remValue * 1rem;
}

@mixin hideInput() {
  position: absolute;
  z-index: -1;
  opacity: 0;
  width: 0;
  height: 0;
}

@mixin iconWithGradient($gradient-color) {
  background: $gradient-color;
  background-clip: text;
  color: transparent;
}

$dark-shade-100: rgba(31, 32, 65, 1.0);
$dark-shade-75: rgba(31, 32, 65, 0.75);
$dark-shade-50: rgba(31, 32, 65, 0.5);
$dark-shade-25: rgba(31, 32, 65, 0.25);
$dark-shade-10: rgba(31, 32, 65, 0.10);
$dark-shade-5: rgba(31, 32, 65, 0.05);

$grey: #F4F4F6;

$purple: #BC9CFF;
$purple-50: #BC9CFF50;
$purple-25: #BC9CFF25;

$green: #6FCF97;

$jordy-blue: #8BA4F9;
$jordy-blue-50: #8BA4F950;
$jordy-blue-25: #8BA4F925;

$sky-blue: #66D2EA;

$primary-gradient: linear-gradient(180deg, $purple 0%, $jordy-blue 100%);
$primary-gradient-50: linear-gradient(180deg, $purple-50 0%, $jordy-blue-50 100%);
$primary-gradient-25: linear-gradient(180deg, $purple-25 0%, $jordy-blue-25 100%);

$secondary-gradient: linear-gradient(180deg, $green 0%, $sky-blue 100%);

$small: toRem(150);
$medium: toRem(266);
$big: toRem(320);

$xs: toRem(768);
$sm: toRem(800);
$md: toRem(900);
$lg: toRem(1000);
$xl: toRem(1120);
$xxl: toRem(1200);

$checkbox-size: toRem(20);
$checkbox-margin: toRem(10);

$range-slider-height: toRem(6);
$className: 'button';

.#{$className} {
  display: block;
  font-family: Montserrat, Arial, sans-serif;
  font-weight: 700;
  font-size: toRem(12);
  line-height: toRem(14.63);
  text-transform: uppercase;
  text-decoration: none;
  cursor: pointer;

  &__body {
    height: 100%;
    position: relative;
    display: flex;
    align-items: center;
  }

  &__text {
    margin: 0 auto;
	  color: $purple;
  }

  &:hover, &.#{$className}_hovered {
    .#{$className}__text {
     color: $dark-shade-50;
    }
  }

  &_filled,
  &_bordered,
  &_bordered-header,
  &_long,
  &_long-header {
    border-radius: toRem(22);

    .#{$className}__text {
      padding: 0 toRem(18);
    }
  }

  &_filled,
  &_bordered,
  &_long {
    height: toRem(44);
  }

  &_filled,
  &_bordered {
    min-width: toRem(99);
  }

  &_bordered,
  &_bordered-header {
    border: solid toRem(2) transparent;
    background-image: linear-gradient(white, white), $primary-gradient;
    background-origin: border-box;
    background-clip: content-box, border-box;
      
    &:hover, &.#{$className}_hovered {
      background-image: linear-gradient(white, white), $primary-gradient-50;

      .#{$className}__text {
        color: $purple;
      }
    }
  }

  &_filled,
  &_long,
  &_long-header {
    background: $primary-gradient;

    .#{$className}__text {
      color: white;
    }

    &:hover, &.#{$className}_hovered  {
      opacity: 0.5;

      .#{$className}__text {
        color: white;
      }
    }
  }

  &_long {
    max-width: toRem(320);
    width: 100%;
  }

  &_long-header,
  &_bordered-header {
    height: toRem(34);
  }

  &_bordered-header {
    min-width: toRem(87);
    width: 100%;

    @media (min-width: $xl) {
      width: initial;
    }
  }

  &_long-header {
    min-width: toRem(196);
    width: 100%;

    @media (min-width: $xl) {
      width: initial;
    }
  }

  &__icon {
    position: absolute;
    width: toRem(24);
    height: toRem(24);
    right: toRem(10);
    color: white;
  }
}