$htmlSize: 14;

@function toRem($value) {
	$remValue: calc($value / $htmlSize); 
	@return $remValue * 1rem;
}

@mixin hideInput() {
  position: absolute;
  z-index: -1;
  opacity: 0;
  width: 0;
  height: 0;
}

@mixin iconWithGradient($gradient-color) {
  background: $gradient-color;
  background-clip: text;
  color: transparent;
}

$dark-shade-100: rgba(31, 32, 65, 1.0);
$dark-shade-75: rgba(31, 32, 65, 0.75);
$dark-shade-50: rgba(31, 32, 65, 0.5);
$dark-shade-25: rgba(31, 32, 65, 0.25);
$dark-shade-10: rgba(31, 32, 65, 0.10);
$dark-shade-5: rgba(31, 32, 65, 0.05);

$grey: #F4F4F6;

$purple: #BC9CFF;
$purple-50: #BC9CFF50;
$purple-25: #BC9CFF25;

$green: #6FCF97;

$jordy-blue: #8BA4F9;
$jordy-blue-50: #8BA4F950;
$jordy-blue-25: #8BA4F925;

$sky-blue: #66D2EA;

$primary-gradient: linear-gradient(180deg, $purple 0%, $jordy-blue 100%);
$primary-gradient-50: linear-gradient(180deg, $purple-50 0%, $jordy-blue-50 100%);
$primary-gradient-25: linear-gradient(180deg, $purple-25 0%, $jordy-blue-25 100%);

$secondary-gradient: linear-gradient(180deg, $green 0%, $sky-blue 100%);

$small: toRem(150);
$medium: toRem(266);
$big: toRem(320);

$xs: toRem(768);
$sm: toRem(800);
$md: toRem(900);
$lg: toRem(1000);
$xl: toRem(1120);
$xxl: toRem(1200);

$checkbox-size: toRem(20);
$checkbox-margin: toRem(10);

$range-slider-height: toRem(6);
$className: 'input-field';

.#{$className} {
	max-width: toRem(320);
	width: 100%;

	&__header {
		margin-bottom: toRem(4);
	}

	&__main-field {
		position: relative;
		display: flex;

    &:hover {
      .#{$className}__input {
        border-color: $dark-shade-50;
      }
    }
	}

	&__input {
		border: 1px solid $dark-shade-25;
		box-sizing: border-box;
		border-radius: toRem(4);
		height: toRem(44);
		width: 100%;
		padding-left: toRem(14);
    padding-right: toRem(91);
    text-overflow: ellipsis;
    white-space: nowrap;
    overflow: hidden;
    font-family: Montserrat, Arial, sans-serif;
    font-weight: 400;
    color: $dark-shade-75;
    font-size: toRem(14);
    line-height: toRem(18);

		&:hover, &:focus {
			border-color: $dark-shade-50;
			outline: none;
		}

		&::placeholder {
			color: $dark-shade-25;
		 }
	}

	&__button {
		position: absolute;
    background-color: white;
    z-index: 1;
		right: toRem(10);
		align-self: center;
		color: $dark-shade-50;
		width: toRem(24);
		height: toRem(24);
    &::before {
      font-size: toRem(24);
      font-family: 'Material Icons', Arial, sans-serif;
	  }

    &_expandable::before {
      content: 'expand_more';
    }

    &_subscribable::before {
      content: 'arrow_forward';
    }

    &_primary {
      @include iconWithGradient($primary-gradient);
    }

    &_secondary {
      color: $dark-shade-75;
    } 
}

	&_hovered {
		.#{$className}__input {
			border-color: $dark-shade-50;
		}
	}

	&_opened {
		.#{$className}__input {
			border-bottom-left-radius: 0;
			border-bottom-right-radius: 0;
			border-color: $dark-shade-50;
		}
	}

  &_big {
    max-width: toRem(320);
  }

  &_medium {
    max-width: toRem(266);
  }

  &_small {
    max-width: toRem(150);

    .#{$className}__input {
      padding-right: toRem(30);
    }
  }
}

